import './RangeTime.scss';

import React, { useState, useLayoutEffect, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { CommonHelper } from 'helper/common.helper';
import { Slider } from 'components/bases/Slider/Slider';

const MAX_DATE_SECOND = 86400;
const TIME_SPACE = { Seconds: 1, Minutes: 60, Hours: 3600 };

// Slider Time 24h on date with type Hours, Minutes, Seconds
export const RangeTime = ({
    type,
    timeStart,
    timeEnd,
    inDate,
    stepWithType,
    timeFormat,
    onChange,
    onAfterChange,
    disabled,
}) =>
{
    const [value, setValue] = useState();
    const displayMap = useRef();
    const sliderRef = useRef();
    const timeSpace = TIME_SPACE[type];
    const MAX = parseInt(MAX_DATE_SECOND / timeSpace);

    const formatRangeTime = (time) => moment(time, 'HH:mm:ss').format('HH:mm:ss');

    useEffect(()=>
    {
        let start = convertTimeToNumber(formatRangeTime(timeStart), type);
        let end = convertTimeToNumber(formatRangeTime(timeEnd), type);

        if (start === MAX - 1)
        {
            start = MAX;
        }
        if (end === MAX - 1)
        {
            end = MAX;
        }

        if (sliderRef.current)
        {
            sliderRef.current.setValueSlider([start, end]);
        }

        setValue([start,end]);
    }, [timeStart, timeEnd, MAX, type]);

    useEffect(()=>
    {
        displayMap.current = getDisplayMap();
    }, [type]);

    const convertTimeToNumber = (time, type) =>
    {
        // type : Hours , Minutes, Seconds
        const typeConvert = 'as' + type;

        // moment.duration(time).asHours() .asMinutes() .asSeconds()
        return parseInt(moment.duration(time)[typeConvert]());
    };

    const convertNumberToTime = (timeNumb) =>
    {
        const timeSpace = TIME_SPACE[type];
        inDate = inDate ? inDate : undefined;

        // moment toDay with 0:00:00
        const toDayNumb = moment(moment(inDate).format('LL')).format('X');

        return moment.unix(parseInt(toDayNumb) + (timeNumb * timeSpace));
    };

    const displayTime = (timeNumber, typeFormat = null) =>
    {
        timeNumber = timeNumber === MAX ? timeNumber - 1 : timeNumber;
        const timeSpace = TIME_SPACE[type];

        // should return time
        return moment.utc(timeNumber * 1000 * timeSpace).format(typeFormat || 'LTS');
    };

    const getDisplayMap = ()=>
    {
        const map = {};
        for (let i = 0; i <= 23; i++)
        {
            map[parseInt(i * 4.2)] = moment.utc(i * 3600 * 1000).format('hhA');
        }
        return map;
    };

    const handleChangeValue = (value) =>
    {
        const [start, end ] = value;
        const valueShow = CommonHelper.clone(value);
        if (start === MAX)
        {
            valueShow[0]--;
        }
        if (end === MAX)
        {
            valueShow[1]--;
        }

        setValue(value);

        onChange && onChange(valueShow);
    };

    const handleAfterChange = (value) =>
    {
        let [start, end] = CommonHelper.clone(value);
        if (start === MAX)
        {
            start--;
        }
        if (end === MAX)
        {
            end--;
        }

        const timeStart = convertNumberToTime(start);
        const timeEnd = convertNumberToTime(end);

        onAfterChange(start < end ? [timeStart, timeEnd] : [timeEnd, timeStart]);
    };

    return (()=>
    {
        if (!value)
        {
            return <></>;
        }

        const [start, end] = value;

        const styleMark = {
            color: disabled ? 'var(--text-color-dark)' : 'var(--text-color-light)' ,
            fontSize: '0.75rem',
        };

        return (
            <div className={'range-time-container'}>
                <Slider
                    ref={sliderRef}
                    disabled={disabled}
                    min={0}
                    max={MAX}
                    step={stepWithType}
                    thumbSize={'12px'}
                    rangeSize={'3px'}
                    rangeDefaultColor={'white'}
                    trackStyle={{
                        backgroundColor: 'white',
                        opacity: 'unset',
                        borderRadius: '4px',
                    }}
                    value={value}
                    displayMap={displayMap.current}
                    marks={{
                        0: {
                            label: displayTime(0, timeFormat),
                            style: styleMark,
                        },
                        100: {
                            label: displayTime(MAX - 1, timeFormat),
                            style: styleMark,
                        },
                    }}
                    range
                    showIndicator
                    onChange={handleChangeValue}
                    onAfterChange={handleAfterChange}
                />
                <div
                    className={'range-time-display'}
                    style={{ ...disabled && { color: 'var(--text-color-dark)' } }}
                >
                    {
                        (start <= end)
                            ? (
                            `${displayTime(start, timeFormat)} - ${displayTime(end, timeFormat)}`
                                )
                            : (
                            `${displayTime(end, timeFormat)} - ${displayTime(start,timeFormat)}`
                                )
                    }
                </div>
            </div>
        );
    })();
};


RangeTime.propTypes = {
    inDate: PropTypes.instanceOf(moment), // string or moment
    timeStart: PropTypes.instanceOf(moment), // string or moment
    timeEnd: PropTypes.instanceOf(moment), // string or moment
    onAfterChange: PropTypes.func,
    onChange: PropTypes.func,
    timeFormat: PropTypes.string,
    stepWithType: PropTypes.number,
    type: PropTypes.oneOf(['Hours', 'Minutes', 'Seconds']),
    disabled: PropTypes.bool,
};

RangeTime.defaultProps = {
    inDate: undefined,
    onAfterChange: null,
    onChange: null,
    timeFormat: 'hh:mm A',
    stepWithType: 1,
    type: 'Minutes',
    disabled: false,
    timeStart: moment().startOf('date'),
    timeEnd: moment().endOf('date') ,
};
