import './DateTimePicker.scss';

import React, { Component, forwardRef } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { Constants } from 'constant/Constants';

import { withI18n } from 'components/bases/I18n/withI18n';
import { AnchorOverlay } from 'components/bases/Modal/AnchorOverlay';

import { DateTimePickerController } from './DateTimePickerController';
import { InputMoment } from './InputMoment';
import withFormat from './withFormat';

class DateTimePicker extends Component
{
    state = {
        isVisible: this.props.isVisible,
        value: null,
        rangeTime: [0, 24],
        inputValue: null,
        isFormat: true,
        overlayPosition: { y: 0, position: 'bottom' },
        overlayElement: null,
        tab: 0,
    };

    constructor(props)
    {
        super(props);
        this.inputRef = React.createRef();
        this.containerRef = React.createRef();
        this.searchRef = React.createRef();
    }

    componentDidMount()
    {
        if (this.props.rangeTime && this.props.rangeTime.length === 2)
        {
            let start = this.props.rangeTime[0];
            let end = this.props.rangeTime[1];

            if (start > end)
            {
                start = end;
                end = this.props.rangeTime[0];
            }

            if (start < 0 || start > 23)
            {
                start = 0;
            }

            if (end < 0 || end > 23)
            {
                end = 23;
            }

            this.setState({ rangeTime: [start, end] });
        }


        if (this.props.value)
        {
            this.setState({ value: moment(this.props.value) });
        }
        else
        {
            // this.setState({ value: moment() });
        }
    }

    componentDidUpdate(prevProps, prevState)
    {
        if (prevState.isVisible !== this.state.isVisible && !this.state.isVisible)
        {
            this.setState({ inputValue: null });
        }
    }

    // DateTimePicker received a new date whenever Edit Form was called, but have no method to set the new date to the input box, so here it is
    static getDerivedStateFromProps = (nextProps, prevState) =>
    {
        if (!nextProps.onChange && !nextProps.value)
        {
            return prevState;
        }
        if (!nextProps.value)
        {
            return { value: null };
        }

        if (moment(nextProps.value).diff(prevState.value, 'minutes') !== 0 && nextProps.value)
        {
            return { value: moment(nextProps.value) }; // return new state
        }

        return null; // don't change state
    };

    closeControl = () => this.setState({ isVisible: false });

    handleControlClick = () => (!this.props.disabled && !this.props.readOnly) && this.setState({ isVisible: !this.state.isVisible });

    onBlur = () =>
    {
        const { isVisible, isFormat } = this.state;
        isVisible && this.closeControl();
        !isFormat && this.setState({ isFormat: true });
    };

    handleChange = (val, rangeTime = null) =>
    {
        if (!this.props.showTimeSelect && !this.props.showTimeSelectOnly)
        {
            val = val.startOf('date');
        }

        // fake event, purpose to assign target for event, not to fire event, search for another approach later
        const event = new Event('input', { bubbles: true });
        this.inputRef.current && this.inputRef.current.dispatchEvent(event);
        event.target.value = val;

        this.props.customOnChange && this.props.customOnChange(event);

        this.setState({ value: val, rangeTime });
        this.props.onChange && this.props.onChange(val, rangeTime);
    };

    formatValueControl = () =>
    {
        if (!this.state.value)
        {
            return '';
        }

        let valueFormat = '';

        if (this.props.showTimeSelectOnly)
        {
            valueFormat = this.state.value.format('LT');
        }
        else
        {
            if (this.props.showTimeRange)
            {
                const values = [];
                this.state.rangeTime.forEach((time) =>
                {
                    const dateAdjust = (time > 24) ? this.state.value.get('date') + 1 : (time < 0 ? this.state.value.get('date') - 1 : this.state.value.get('date'));
                    const timeAdjust = (time > 24) ? time - 24 : (time < 0 ? time + 24 : time);

                    values.push(new moment().set('date', dateAdjust).hour(timeAdjust).minute(0).format('L hA'));
                });

                valueFormat = values.join(' - ');
            }
            else if (this.props.showTimeSelect)
            {
                valueFormat = this.state.value.format('L LT');
            }
            else
            {
                valueFormat = this.state.value.format('L');
            }
        }

        return valueFormat;
    };

    handleClearValueClick = (e) =>
    {
        e.stopPropagation();
        this.setState({ value: null });
        this.props.onChange && this.props.onChange();
    };

    handleValue = (value) =>
    {
        this.props.customOnChange && this.props.customOnChange(value);
        this.setState({ isFormat: false });

        const longDateFormatL = moment.localeData().longDateFormat('L');
        const longDateFormatLT = longDateFormatL + ' HH:mm';
        const timeFormat = 'HH:mm';
        const format = this.props.showTimeSelectOnly ? timeFormat : longDateFormatLT;
        this.props.setFormat(format);

        const defaultDate = this.props.value ? moment(this.props.value) : moment();
        const defaultFormattedDate = defaultDate.format(format);

        value = this.props.formatValue(value);
        const cloneValue = value;

        // format value like defaullDate
        if (value.length < defaultFormattedDate.length)
        {
            value = value + defaultFormattedDate.substring(value.length, defaultFormattedDate.length);
        }

        if (this.props.showTimeSelectOnly)
        {
            value = defaultDate.format(longDateFormatL) + ' ' + value;
        }

        const compareValue = moment.utc(value, longDateFormatLT);
        if (this.props.minDate)
        {
            const minDate = moment.utc(moment(this.props.minDate).format(longDateFormatLT), longDateFormatLT);
            const isBefore = moment(compareValue).isBefore(minDate);
            value = isBefore ? moment(minDate).format(format) : value;
        }
        else if (this.props.maxDate)
        {
            const maxDate = moment.utc(this.props.maxDate, longDateFormatLT);
            const isAfter = moment(compareValue).isAfter(maxDate);
            value = isAfter ? moment(maxDate).format(format) : value;
        }

        const formattedValue = moment.utc(value, longDateFormatLT);
        if (formattedValue.isValid())
        {
            this.props.onChange && this.props.onChange(formattedValue);
        }

        this.setState({ inputValue: cloneValue, tab: cloneValue.length >= longDateFormatL.length ? 1 : 0 });
    }

    handleKeyDown = (event) =>
    {
        const enterKey = event.keyCode === Constants.KEYS.ENTER;
        enterKey && this.setState({ isFormat: true, isVisible: false });
    }

    reFormat = (value, subValue, index) => value.slice(0, index) + subValue + value.slice(index + subValue.length);

    handleOverlayPosition = () =>
    {
        const element = this.state.overlayElement;
        if (!element || !this.containerRef.current)
        {
            return;
        }

        const position = this.containerRef.current.getBoundingClientRect();
        const height = element.offsetHeight;
        const top =
            position.y + height > window.innerHeight
                ? { y: position.bottom - height, position: 'bottom' }
                : { y: position.y, position: 'top' };

        this.setState({ overlayPosition: top });
    };

    render()
    {
        const valueFormat = this.formatValueControl();

        return (
            <div
                ref={this.containerRef}
                style={{ width: '100%' }}
                className={`dtp-container ${this.props.disabled ? 'dtp-container-disabled' : ''}`}
                tabIndex={0}
            >
                <DateTimePickerController
                    innerRef={elm =>
                    {
                        this.props.innerRef && this.props.innerRef(elm);
                        this.inputRef.current = elm;
                        elm && this.props.setInput(elm);
                    }}
                    className={this.props.className}
                    isVisible={this.state.isVisible}
                    isSmartForm={this.props.isSmartForm}
                    placeholder={this.props.t(this.props.placeholder)}
                    value={(this.state.isFormat || !this.state.isVisible) ? valueFormat : (typeof this.state.inputValue === 'string' ? this.state.inputValue : valueFormat)}
                    disabled={this.props.disabled}
                    name={this.props.name}
                    clearable={this.props.clearable}
                    showTimeSelectOnly={this.props.showTimeSelectOnly}
                    readOnly={this.props.readOnly}
                    searchable
                    onSearch={val => this.setState({ searchKey: val })}
                    onChange={this.handleValue}
                    onClearValueClick={this.handleClearValueClick}
                    onControlClick={this.handleControlClick}
                />

                {
                    this.state.isVisible && !this.props.disabled && (
                        <AnchorOverlay
                            anchorEl={this.containerRef}
                            className='dtp-overlay'
                            width={'20rem'}
                            backdrop={false}
                            onBackgroundClick={this.onBlur}
                        >
                            <InputMoment
                                moment={this.state.value}
                                rangeTime={this.state.rangeTime}
                                minStep={1}
                                hourStep={1} // default
                                showTimeSelect={this.props.showTimeSelect && !this.props.showTimeRange} // default
                                showTimeSelectOnly={this.props.showTimeSelectOnly && !this.props.showTimeRange}
                                showTimeRange={this.props.showTimeRange}
                                minDate={this.props.minDate && moment(this.props.minDate)}
                                maxDate={this.props.maxDate && moment(this.props.maxDate)}
                                minTime={this.props.minTime && moment(this.props.minTime)}
                                maxTime={this.props.maxTime && moment(this.props.maxTime)}
                                tab={this.state.tab}
                                onTab={tab => this.setState({ tab })}
                                onChange={this.handleChange}
                            />
                        </AnchorOverlay>
                    )}
            </div>
        );
    }
}

DateTimePicker.propTypes = {
    className: PropTypes.string,
    onChange: PropTypes.func,
    value: PropTypes.any,
    rangeTime: PropTypes.arrayOf(PropTypes.number), // not support typeable dtg
    disabled: PropTypes.bool,
    showTimeSelect: PropTypes.bool,
    showTimeSelectOnly: PropTypes.bool,
    showTimeRange: PropTypes.bool, // not support typeable dtg
    clearable: PropTypes.bool,
    placeholder: PropTypes.string,
    minDate: PropTypes.any,
    maxDate: PropTypes.any,
    minTime: PropTypes.any, // not support typeable dtg
    maxTime: PropTypes.any, // not support typeable dtg
    isVisible: PropTypes.bool,
    isSmartForm: PropTypes.bool,
    // SmartForm
    innerRef: PropTypes.any,
    customOnChange: PropTypes.any,
    name: PropTypes.string,
    readOnly: PropTypes.bool,
};

DateTimePicker.defaultProps = {
    className: '',
    // onChange: () =>
    // {
    // },
    disabled: false,
    clearable: false,
    showTimeRange: false,
    isVisible: false,
    isSmartForm: false,
};

DateTimePicker = withI18n(DateTimePicker);
DateTimePicker = withFormat(DateTimePicker);
// eslint-disable-next-line react/display-name
export default forwardRef((props, ref) => (
    <DateTimePicker
        {...props}
        innerRef={ref}
    />
));

export { DateTimePicker };
