import './InputMoment.scss';

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import moment from 'moment';

import { T } from 'components/bases/Translate/Translate';

import RangeDateTime from './RangeDateTime';
import { Time } from './Time';
import { Calendar } from './Calendar';

export class InputMoment extends Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            rangeTime: props.rangeTime || [0, 24],
        };
    }

    componentDidMount()
    {
        document.addEventListener('wheel', this.preventScroll, { capture: true, passive: false });
    }

    componentWillUnmount()
    {
        document.removeEventListener('wheel', this.preventScroll, { capture: true, passive: false });
        this.props.onTab(0);
    }

    preventScroll = event =>
    {
        event.preventDefault();
        event.stopPropagation();
    };

    handleClickTab = (e, tab) =>
    {
        e.preventDefault();
        this.props.onTab(tab);
    };

    handleCalendarChange = (value) =>
    {
        const { onChange, showTimeRange } = this.props;
        const { rangeTime } = this.state;

        if (showTimeRange)
        {
            onChange && onChange(value, rangeTime);
        }
        else
        {
            onChange && onChange(value);
        }
    };

    handleRangeTimeChange = (rangeTime) =>
    {
        if (Array.isArray(rangeTime))
        {
            this.props.onChange && this.props.onChange(this.props.moment, rangeTime);
            this.setState({ rangeTime });
        }
    };

    render()
    {
        const {
            className,
            moment: m,
            minStep,
            hourStep,

            showTimeSelect,
            showTimeSelectOnly,
            showTimeRange,

            disabled,
            minDate,
            maxDate,
            minTime,
            maxTime,

            tab,
        } = this.props;

        const cls = cx('m-input-moment', className);

        return (
            <div className={cls}>
                {
                    showTimeSelect && !showTimeSelectOnly && (
                        <div className="options">
                            <button
                                type="button"
                                className={cx('ion-calendar im-btn', { 'active': tab === 0 || tab === 'month' || tab === 'year' })}
                                disabled={disabled}
                                onClick={(e) => this.handleClickTab(e, 0)}
                            >
                                <T>Ngày</T>
                            </button>
                            <button
                                type="button"
                                className={cx('ion-clock im-btn', { 'active': tab === 1 })}
                                disabled={disabled}
                                onClick={(e) => this.handleClickTab(e, 1)}
                            >
                                <T>Giờ</T>
                            </button>
                        </div>
                    )}
                <div className="tabs">
                    {
                        (!showTimeSelectOnly && tab === 0) && (
                            <Calendar
                                className={'tab'}
                                value={m}
                                disabled={disabled}
                                minDate={minDate}
                                maxDate={maxDate}
                                onChange={this.handleCalendarChange}
                            />
                        )}
                    {
                        (showTimeSelectOnly || (showTimeSelect && tab === 1)) && (
                            <Time
                                className={'tab'}
                                moment={m || moment(new Date())}
                                minStep={minStep}
                                hourStep={hourStep}
                                minTime={minTime}
                                maxTime={maxTime}
                                onChange={this.props.onChange}
                            />
                        )}
                    {
                        showTimeRange && (
                            <RangeDateTime
                                rangeTime={this.state.rangeTime}
                                onAfterChange={this.handleRangeTimeChange}
                            />
                        )}
                </div>
            </div>
        );
    }
}

InputMoment.propTypes = {
    className: PropTypes.string,

    moment: PropTypes.object,
    rangeTime: PropTypes.arrayOf(PropTypes.number),

    minStep: PropTypes.number,
    hourStep: PropTypes.number,

    showTimeSelect: PropTypes.bool,
    showTimeSelectOnly: PropTypes.bool,
    showTimeRange: PropTypes.bool,

    minDate: PropTypes.any,
    maxDate: PropTypes.any,
    minTime: PropTypes.any,
    maxTime: PropTypes.any,
    onChange: PropTypes.func,

    tab: PropTypes.number,
};

InputMoment.defaultProps = {
    minStep: 1,
    hourStep: 1,
    showTimeSelect: false,
    showTimeSelectOnly: false,
};
