import { useContext } from 'react';
import { I18nContext } from 'components/bases/I18n/I18nContext';
import { DomainContext } from './DomainContext';

export const useI18n = () =>
{
    const i18n = useContext(I18nContext);
    const domainContext = useContext(DomainContext);
    const domain = domainContext?.['domain'];

    const language = i18n?.language || 'vi';

    const t = (text, params = []) =>
    {
        return i18n ? i18n.t(text, params, domain) : text;
    };

    const setLocale = () =>
    {
        i18n && i18n.setLocale();
    };

    const setLanguage = (lang) =>
    {
        i18n && i18n.setLanguage(lang);
    };

    const addTranslates = (trans) =>
    {
        i18n && i18n.addTranslates(trans);
    };

    return {
        language,
        setLocale,
        setLanguage,
        addTranslates,
        t,
    };
};
