import React, { createContext, useEffect, useState } from 'react';
import Cookies from 'js-cookie';

import HttpClient from 'helper/http.helper';
import { Loading } from 'components/bases/Modal/Loading';

const http = new HttpClient();

export const TenantContext = createContext();

const TenantProvider = (props) =>
{
    const [tenant, setTenant] = useState({});
    const [loading, setLoading] = useState(true);

    useEffect(() =>
    {
        if (props.apiURL)
        {
            http.get(props.apiURL).then((config) =>
            {
                // if config has public access token, write it down
                if (config.vdms.publicAccessToken)
                {
                    Cookies.set('vdmsPublicAccesstoken', config.vdms.publicAccessToken);
                }

                setTenant(config);
                setLoading(false);
            });
        }
        else
        {
            setLoading(false);
        }
    }, []);

    return (
        <TenantContext.Provider value={{ config: tenant }}>
            {loading ? <Loading fullscreen /> : props.children}
        </TenantContext.Provider>
    );
};

export { TenantProvider };
