import React from 'react';
import PropTypes from 'prop-types';
import { FAIcon } from '@vbd/vicon';

import { Row } from 'components/bases/Layout/Row';
import { CheckBox } from 'components/bases/CheckBox/CheckBox';
import { Column } from 'components/bases/Layout/Column';

import { UserAvatar } from './UserAvatar';

import './UserListItem.scss';

export function UserListItem(props)
{
    const user = props.user;
    const { info, actions } = user;

    const handleAction = (e, action, user) =>
    {
        e.stopPropagation();
        action.onClick && action.onClick(user.userId);
    };

    return (
        <Row
            className='user-list-item'
            mainAxisAlignment="space-between"
            crossAxisAlignment="center"
        >
            <Column onClick={() => props.onClick && props.onClick(user)}>
                <Row>
                    {
                        !props.hideCheckbox && (
                            <CheckBox
                                checked={props.value}
                            />
                        )}
                    <UserAvatar
                        src={user.avatar}
                        lastActiveAt={user.lastActiveAt}
                    />
                    <Column>
                        <div className='user-name'>{user.displayName}</div>
                        {info && <span className="info">{info}</span>}
                    </Column>
                </Row>
            </Column>
            {
                actions && (
                    <Column
                        crossAxisAlignment="end"
                        crossAxisSize="min"
                        className="actions"
                    >
                        <Row>
                            {
                                actions.map((action, index) =>
                                {
                                    if (!action.disabled)
                                    {
                                        return (
                                            <Column
                                                key={index}
                                                className="item"
                                                mainAxisAlignment={'center'}
                                            >
                                                <FAIcon
                                                    size="1rem"
                                                    icon={action.icon}
                                                    type="solid"
                                                    onClick={(e) => handleAction(e, action, user)}
                                                />
                                            </Column>
                                        );
                                    }
                                })
                            }
                        </Row>
                    </Column>
                )}
        </Row>
    );
}

UserListItem.propTypes = {
    hideCheckbox: PropTypes.bool,
    user: PropTypes.object,
    value: PropTypes.bool,
    info: PropTypes.any,
};

export default UserListItem;
