import React, { createContext, useContext } from 'react';
import Moment from 'react-moment';
import moment from 'moment';

import { TenantContext } from 'components/bases/Tenant/TenantContext';
import { DomainContext } from './DomainContext';

export const I18nContext = createContext();

const I18nProvider = (props) =>
{
    const tenantContext = useContext(TenantContext);
    const locale = localStorage.getItem('locale') || tenantContext.config['locale'] || 'vi';

    const setLocale = (lo) =>
    {
        if (!lo)
        {
            lo = locale;
        }

        if (lo !== 'en')
        {
            require('moment/locale/' + lo);
        }

        Moment.globalLocal = lo;
        moment.updateLocale(lo);
    };

    const language = localStorage.getItem('language') || tenantContext.config['language'] || 'vi';
    const translates = props.translates(language);

    const setLanguage = (newLanguage) =>
    {
        localStorage.setItem('language', newLanguage);

        // also set locale
        localStorage.setItem('locale', newLanguage);

        window.location.reload();
    };

    const addTranslate = (key, value) =>
    {
        translates[key] = value;
    };

    const addTranslates = (trans) =>
    {
        if (Array.isArray(trans))
        {
            trans.forEach((d) =>
            {
                translates[d.key] = d.value;
            });
        }
    };

    const translate = (text, params, domain) =>
    {
        if (typeof text !== 'string')
        {
            return text;
        }

        let output = text;
        if (translates)
        {
            if (domain && translates[domain] && translates[domain][text])
            {
                output = translates[domain][text];
            }
            else if (translates[text])
            {
                output = translates[text] ;
            }
        }

        if (Array.isArray(params))
        {
            params.forEach((p, index) =>
            {
                output = output.replace(`%${index}%`, p);
            });
        }

        return output;
    };

    return (
        <I18nContext.Provider
            value={{
                locale, setLocale, language, setLanguage, t: translate,
                addTranslate, addTranslates,
            }}
        >
            {props.children}
        </I18nContext.Provider>
    );
};

export { I18nProvider };
